.zantimes-accordion {
  padding-top: 30px;
  padding-bottom: 30px;
}
.zantimes-accordion__heading {
  margin-bottom: 3rem;
  color: #1D0056;
}
.zantimes-accordion-item {
  margin-bottom: 1.5rem;
  border-bottom: 2px solid #f0f0f0;
}
.zantimes-accordion-item:last-of-type {
  margin-bottom: 0;
}
.zantimes-accordion-item__heading {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-right: 3.5rem;
  padding-bottom: 1.5rem;
  font-size: 20px;
  font-weight: 700;
  color: #000000;
  cursor: pointer;
}
.zantimes-accordion-item__heading:after {
  content: "";
  position: absolute;
  right: 20px;
  margin-left: 10px;
  margin-right: 10px;
  width: 20px;
  height: 20px;
  background: url("../../assets/images/svg/arrow-down.svg") no-repeat center center;
  background-size: 100%;
  transition: transform 0.2s ease-in-out;
}
.zantimes-accordion-item__heading[aria-expanded=true]:after {
  transform: rotate(180deg);
}
.zantimes-accordion-item__body {
  padding: 0 20px 30px;
}
.zantimes-accordion-item__body p {
  margin-bottom: 0;
}
.zantimes-accordion-item ul {
  margin-top: 1rem;
}
.zantimes-accordion-item ul li a {
  color: #4008ac;
}
@media screen and (min-width: 992px) {
  .zantimes-accordion {
    padding-top: 80px;
    padding-bottom: 80px;
  }
  .zantimes-accordion-item__body {
    padding-right: 165px;
  }
}
@media screen and (max-width: 767px) {
  .zantimes-accordion-item__heading {
    padding-right: 80px;
  }
}